"use client";

import { useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet";
import { Menu, Search, User, ShoppingBag, PackageIcon } from "lucide-react";
import { TrolleyIcon } from "@sanity/icons";
import {
  ClerkLoaded,
  SignedIn,
  SignInButton,
  UserButton,
  useUser,
} from "@clerk/nextjs";
import Form from "next/form";
import useBasketStore from "@/app/(store)/store/store";

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);
  const pathname = usePathname();
  const { user } = useUser();
  const itemCount = useBasketStore((state) =>
    state.items.reduce((total, item) => total + item.quantity, 0)
  );

  const createClerkPasskey = async () => {
    try {
      const response = await user?.createPasskey();
      console.log(response);
    } catch (err) {
      console.log("Error", JSON.stringify(err, null, 2));
    }
  };

  const navigation = [
    { name: "Shoes", href: "/categories/shoes" },
    { name: "Streetwear", href: "/categories/streetwear" },
    { name: "Skateboard", href: "/categories/skateboard" },
    { name: "Snowboard", href: "/categories/snowboard" },
    { name: "Accessori", href: "/categories/accessori" },
    { name: "Marchi", href: "/categories/marchi" },
    { name: "PROMO", href: "/categories/promo" },
    { name: "Gift Card", href: "/gift-card" },
    { name: "Store & Family", href: "/store-family" },
  ];

  const isActive = (href: string) => {
    if (href === "/" && pathname === "/") return true;
    if (href !== "/" && pathname.startsWith(href)) return true;
    return false;
  };

  return (
    <header className="sticky top-0 z-50 w-full bg-white border-b border-gray-100">
      <div className="border-b border-gray-50">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex h-16 items-center justify-between">
            {/* Left - Search (with your working search form) */}
            <div className="flex items-center space-x-4">
              <Form action="/search" className="hidden sm:flex">
                <input
                  type="text"
                  name="query"
                  placeholder="Search for products"
                  className="bg-gray-100 text-gray-800 px-4 py-2 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500 transition-all duration-200"
                />
                <Button variant="ghost" size="icon">
                  <Search className="h-5 w-5" />
                  <span className="sr-only">Search</span>
                </Button>
              </Form>
            </div>

            {/* Centered Logo */}
            <div className="flex items-center">
              <Link href="/" className="flex items-center">
                <span className="font-serif font-bold text-2xl tracking-tight text-blue-600">
                  URBAN
                </span>
              </Link>
            </div>

            {/* Right - User, Basket, Orders, Auth */}
            <div className="flex items-center space-x-4">
              {/* Auth and Orders */}
              <ClerkLoaded>
                <SignedIn>
                  <Link
                    href="/orders"
                    className="hidden md:flex items-center space-x-2 bg-blue-500 hover:bg-blue-600 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200"
                  >
                    <PackageIcon className="w-5 h-5" />
                    <span>My Orders</span>
                  </Link>
                </SignedIn>
                {user ? (
                  <div className="flex items-center space-x-2">
                    <Button
                      variant="ghost"
                      size="icon"
                      className="hidden md:flex"
                    >
                      <UserButton />
                      <span className="sr-only">Account</span>
                    </Button>
                    <div className="hidden md:block text-sm">
                      <p className="text-gray-500">Welcome back</p>
                      <p className="font-semibold text-gray-800">
                        {user.fullName}
                      </p>
                    </div>
                  </div>
                ) : (
                  <div className="hidden md:block bg-blue-500 hover:bg-blue-600 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200">
                    <SignInButton mode="modal">Sign In</SignInButton>
                  </div>
                )}
                {user?.passkeys?.length === 0 && (
                  <button
                    onClick={createClerkPasskey}
                    className="hidden md:block bg-white hover:bg-blue-600 hover:text-white text-blue-600 font-medium py-2 px-4 rounded-lg border border-blue-300 transition-all duration-200"
                  >
                    Create Passkey
                  </button>
                )}
              </ClerkLoaded>

              {/* Basket */}
              <Link
                href="/basket"
                className="relative flex items-center space-x-2 bg-blue-500 hover:bg-blue-600 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200"
              >
                <TrolleyIcon className="w-5 h-5" />
                <span className="hidden sm:inline">My Basket</span>
                {itemCount > 0 && (
                  <span className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full w-5 h-5 flex items-center justify-center text-xs">
                    {itemCount}
                  </span>
                )}
              </Link>

              {/* Mobile menu */}
              <Sheet open={isOpen} onOpenChange={setIsOpen}>
                <SheetTrigger asChild>
                  <Button variant="ghost" size="icon" className="md:hidden">
                    <Menu className="h-5 w-5" />
                    <span className="sr-only">Toggle menu</span>
                  </Button>
                </SheetTrigger>
                <SheetContent side="right" className="w-[300px] sm:w-[400px]">
                  <nav className="flex flex-col space-y-4 mt-8">
                    {navigation.map((item) => (
                      <Link
                        key={item.name}
                        href={item.href}
                        className="text-lg font-medium hover:text-primary transition-colors"
                        onClick={() => setIsOpen(false)}
                      >
                        {item.name}
                      </Link>
                    ))}
                    <div className="flex flex-col space-y-2 mt-4">
                      <ClerkLoaded>
                        <SignedIn>
                          <Link
                            href="/orders"
                            className="flex items-center space-x-2 bg-blue-500 hover:bg-blue-600 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200"
                          >
                            <PackageIcon className="w-5 h-5" />
                            <span>My Orders</span>
                          </Link>
                        </SignedIn>
                        {user ? (
                          <div className="flex items-center space-x-3">
                            <UserButton />
                            <div className="text-sm">
                              <p className="text-gray-500">Welcome back</p>
                              <p className="font-semibold text-gray-800">
                                {user.fullName}
                              </p>
                            </div>
                          </div>
                        ) : (
                          <div className="bg-blue-500 hover:bg-blue-600 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200">
                            <SignInButton mode="modal">Sign In</SignInButton>
                          </div>
                        )}
                        {user?.passkeys?.length === 0 && (
                          <button
                            onClick={createClerkPasskey}
                            className="bg-white hover:bg-blue-600 hover:text-white text-blue-600 font-medium py-2 px-4 rounded-lg border border-blue-300 transition-all duration-200"
                          >
                            Create Passkey
                          </button>
                        )}
                      </ClerkLoaded>
                    </div>
                  </nav>
                </SheetContent>
              </Sheet>
            </div>
          </div>
        </div>
      </div>

      <div className="hidden md:block">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <nav className="flex items-center justify-center space-x-8 h-12">
            {navigation.map((item) => (
              <Link
                key={item.name}
                href={item.href}
                className={`relative text-sm font-medium transition-colors hover:text-primary pb-3 ${
                  item.name === "PROMO"
                    ? "text-red-600 font-semibold"
                    : "text-gray-700"
                } ${isActive(item.href) ? "text-primary" : ""}`}
              >
                {item.name}
                {isActive(item.href) && (
                  <span className="absolute bottom-0 left-0 right-0 h-0.5 bg-gradient-to-r from-blue-500 via-purple-500 to-pink-500 rounded-full"></span>
                )}
              </Link>
            ))}
          </nav>
        </div>
      </div>
    </header>
  );
}
